@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@1,100&display=swap');

body {
  margin: 0;
  font-family: 'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin-top: 50px;
  background-color: #F5F5F5;
}

.header{
  display: flex;
  align-items: center;
  justify-content:space-between;
  margin-top: 10px;
  margin: 0 auto;
  width: 90%;
}

.searchHeader{
  display: flex;
  align-items: center;
  justify-content:space-between;
  margin-top: 10px;
  margin: 0 auto;
  width: 90%;
}

.footer{
  margin-top: 200px;
  background-color: #FF3939;
  padding-top: 50px;
  padding-bottom: 50px;
}
.instagram{
  display: flex;
  align-items: center;
  color: white;
  font-family: sans-serif;
}

.instagramIcon{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 17.5%;
}

.instagramIcon a{
  color: white;
  text-decoration: none;
}
.footerMenu{
  display: flex;
  align-items: last baseline;
  justify-content: space-between;
  padding: 20px;
}
.footerMenu ul{
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  font-family: sans-serif;
  color: white;
  margin-top: 100px;
}

.footerMenu li{
  width: 33%;
  margin-bottom: 10px;
  box-sizing: border-box;
  list-style-type: none;
  font-size: 12px;
  font-weight: bold;
}

.headerImg{
  display: flex;
  align-items: baseline;
  gap: 30px;
}

.headerImgLogo img{
  width: 272px;
  height: 141px;
}

.headerImgLogo2 img{
  margin-bottom: 8px;
}

.header img{
}

.headerMenu ul{
  display: flex;
  flex-wrap: wrap;
  font-family: sans-serif;
}

.headerMenu li{
  width: 33%;
  margin-bottom: 10px;
  box-sizing: border-box;
  list-style-type: none;
  font-size: 12px;
  font-weight: bold;
}

.search {
  margin-left: -100px;
  margin-top: 30px;
  min-height: 100%;
}

.search input{
  border: 1.5px solid #292929;
  width: 350px;
  padding: 10px;
  padding-left: 30px;
  margin-right: 10px;
}

.search input::placeholder{
  
}

.search input:focus{
  outline: none;
}

.topPageSearch {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 100px 0;
}

.topPageSearch input{
  border: 2px solid #292929;
  width: 450px;
  padding: 15px 30px 15px 40px;
  margin-right: 10px;
}

.topPageSearch input::placeholder{
  
}

.topPageSearch input:focus{
  outline: none;
}

.topPageSearch button{
  background-color: #FF3939;
  border: none;
  color: #FBF9F4;
  padding: 15px;
  width: 160px;
}

.top{
  margin: 0 auto;
  width: 90%;
}

.mainContent{
  margin: 0 auto;
}

.mainContent a{
  text-decoration: none;
  color: black;
}

.artist{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  position: relative;
  width: 90%;
  margin-bottom: 50px;
}

.artistText{
  width: 30%;
}


.favorite{
  position: absolute;
  bottom: 0;
  right: 0;
}


.mainContentTitle, .subContentTitle {
  font-weight: bold;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.mainContentText, .subContentText, .articleSubContent {
  font-family: 'Noto Sans', sans-serif;
}

.mainContentText{
  font-size: 24px;
}

.mainContentAuthor, .subContentAuthor {
  color: #969696;
  margin-top: 10%;
  font-size: 11px;
}

.mainContentDate, .subContentDate {
  color: #969696;
  font-size: 11px;
}

.searchPageHeading{
  text-align: center;
  margin-top: 100px;
  color: #4A4A4A;
  line-height: 30px;
  font-size: 14px;
}

.searchPageForm{
  margin-top: 50px;
  text-align: center;
  background-color: white;
  width: 500px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.35);
}

.searchPageForm input{
  background: none;
  border: none;
  padding: 10px;
  width: 75%;
}

.searchPageForm input:focus{
  outline: none;
}

.searchPageForm button, .headerMenu button{
  background: #FF3939;
  border: none;
  padding: 7px;
  width: 25%;
  margin: 5px 10px 5px 5px;
  color: white;
}

.articlePageHeding{
  color: #969696;
  margin: 100px auto;
  text-align: center;
}

.article{
  margin: 0 auto;
  width: 50%;
}

.articleHeader{
  border-bottom: 0.6px solid;
}

.articleHeader p{
  color: #535353;
}

.articleHeaderDate{
  font-size: 12px;
  font-style: italic;
  margin-top: 30px;
  margin-bottom: 30px;
}

.snsIcon{
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 40px;
}

.snsIcon1{
  background-color: black;
  font-size: 12px;
  color: #FCFEFC;
  padding: 2px;
  width: 220px;
  display: flex;
  font-style: italic;
  font-weight: lighter;
}

.snsIcon1 img{
  width: 40px;
  height: 40px;
}

.snsIcon2{
  background-color: #1459A9;
  font-size: 12px;
  color: #FCFEFC;
  padding: 2px;
  width: 220px;
  display: flex;
  gap: 5px;
  align-items: center;
  font-style: italic;
  font-weight: lighter;
}

.snsIcon2 img{
  width: 20px;
  height: 20px;
}

.snsIcon1 p, .snsIcon2 p{
  color: #FCFEFC;
}

.articleHeadline,.articleFooter {
  margin-top: 50px;
  margin-bottom: 50px;
  line-height: 35px;
}

.articleFooter{
  border-bottom: 0.6px solid;
}

.articleFooter p {
  margin-bottom: 50px;
}

.articleContent p{
  margin-bottom: 50px;
}

.articleVideo{
  width:100%;
  height: 300px;
  background: linear-gradient(black,gray);
  position: relative;
}
.articleLinkToArtistPage{
  width:100%;
  height: 300px;
}

.articleLinkToArtistPage:hover{
  opacity: 0.5;
}

.articleVideoTitle{
  color:#ffffff;
  font-family: 'Noto Sans', sans-serif;
  position: absolute;
  top: 3%;
  left: 10%;
}

.articleVideoPlayButton{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.articleLinkToArtistPageText{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.articleVideoPlayButton button {
  background-color: #FF3939;
  border: none;
  color: #FBF9F4;
  text-align: center;
  width: 70px;
  height: 70px;
  font-size: 30px;
}
.articleSubHeader{
  margin-bottom: 30px;
  font-size: 20px;
}
.articleSubContent{
  font-size: 13px;
  line-height: 25px;
  margin: 20px 0;
}

.loading{
  width: 300px;
  margin: 100px auto;
  text-align: center;
  color: #979797;
  position: relative;
  margin-bottom: 300px;
}

.loading img, .loading svg{
  position: absolute;
}

.loading img {
  width: 50px;
  height: 50px;
  transform: translateY(125%) translateX(-50%);
}

.loading div{
  display: flex;
  justify-content: center;
}


/* SP */
@media (max-width:820px){
  .header{
    align-items: start;
  }
  
  .searchHeader{
    align-items: start;
  }
  .headerImgLogo img{
    width: 50px;
    height: 20px;
  }

  .headerImgLogo2 img{
    width: 20px;
  }
  .headerImg{
    gap: 10px;
  }
  .headerMenu ul{
    height: 60px;
  }
  .headerMenu li{
    display: block;
    width: 40%;
    margin-left: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
    list-style-type: none;
    flex-wrap: wrap;
    font-size: 10px;
    font-weight: 200;
  }
  .topPageSearch {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
  }
  
  .topPageSearch input{
    border: 2px solid #292929;
    width: 200px;
    padding: 15px 30px 15px 40px;
    margin-right: 10px;
  }

  .topPageSearch button{
    padding: 10px;
    width: 80px;
    font-size: 10px;
  }

  .artist{
    flex-wrap: wrap;
  }
  .artistImage img{
    width: 100%;
  }
  .artistText{
    width: 100%;
  }
  .mainContentText{
    font-size: 20px;
  }
  .instagramIcon{
    flex-wrap: wrap;
  }
  .instagramIcon a{
    font-size: 10px;
  }
  .footerMenu{
    display: block;
  }
  .footerMenu ul{
    width: 100%;
    font-size: 10px;
  }

  .footerMenu li{
    font-size: 10px;
  }
  .footerLogo{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .footerLogo img{
    width: 80%;
  }
  .copyright{
    font-size: 10px;
    margin-top: 50px;
    margin-bottom: 0px;
    width: 100%;
  }
  .search {
    margin-left: 0px;
    margin-top: 0px;
    min-height: 50%;
  }
  
  .search input{
    border: 1px solid #292929;
    width: 120px;
    padding: 5px;
    font-size: 10px;
    padding-left: 30px;
    margin-right: 10px;
  }

  .search button{
    font-size: 10px;
    width: 70px;
    padding: 5px;
    text-align: center;
  }

  .article{
    margin: 0 auto;
    width: 80%;
  }

  .article img{
    max-width: 100%;
  }
  .searchPageForm{
    width: 80%;
  }
  .searchPageForm input{
    width: 70%;
  }
  .searchPageForm button{
    width: 30%;
  }
}
